/* Step4.css */

.output-layout {
    padding: 20px;
    border: 1px solid #e1e1e1;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    margin-top: 20px;
}

.section {
    margin-bottom: 20px;
}

.section p {
    font-weight: bold;
    margin-bottom: 8px;
}

.section pre {
    background-color: #f9f9f9;
    padding: 8px;
    border-radius: 5px;
    overflow-x: auto; /* 긴 텍스트가 있을 경우 스크롤바가 생기도록 설정 */
}
